.parent {
  position: relative;
}
.title {
  margin-top: -45px;
  color: white;
  font-size: 20pt;
  font-weight: 800;
  text-shadow: 6px 6px 6px rgb(1, 1, 1);
  line-height: 1;
}
.labelTitle {
  margin-top: auto;
  color: white;
  font-size: 12pt;
  font-weight: 600;
  text-shadow: 6px 6px 6px rgb(1, 1, 1);
  line-height: 1;
}
.containerTitle {
  width: 60%;
  max-width: 300px;
  height: 150px;
  position: relative;
  margin: 9px auto 0;
}
.container {
  background-color: rgba(128, 128, 128, 0.5);
  border-radius: 25px;
  padding: 10px;
  width: 700px;
  height: 100%;
  position: relative;
  left: 49%;
  transform: translate(-50%, calc(-50% + 400px));
}

.typeListContainer {
  position: relative;
  border-radius: 30px;
}

.typeList {
  list-style: none;
  background-color: rgba(0, 0, 0, 0.5);
  color: white;
  padding: 10px;
  border-radius: 25px;
  position: absolute;
  font-size: 10pt;
  top: 35%;
  left: 5%;
  width: 25%;
}

.typeList li {
  padding: 5px;
}

.typeList li:hover {
  background-color: rgba(255, 255, 255, 0.1);
  cursor: pointer;
}
.formDisplay {
  display: flex;
  flex-direction: column;
  gap: 8px;
  margin: 18px;
}

.chosenTypes {
  margin-top: auto;
  color: white;
  font-size: 12pt;
  font-weight: 600;
  line-height: 1;
  text-shadow: 5px 5px 5px rgb(3, 3, 3);
  position: absolute;
  top: 85%;
  left: -5%;
}

.clearTypeButton {
  background-color: #57115d;
  font-size: large;
  font-weight: 900;
  color: white;
  border: none;
  border-radius: 50px;
  padding: 8px;
  cursor: pointer;
  box-shadow: 1px 1px 5px rgba(0, 0, 0, 0.2);
  opacity: 0.9;
}

.clearTypeButton:hover {
  background-color: #3e8e41;
}

input[type="text"] {
  width: 200pt;
  padding: 10px;
  border-radius: 5px;
  border: none;
  box-shadow: 1px 1px 5px rgba(0, 0, 0, 0.2);
  display: block;
  margin: 0 auto;
}
input[type="drop"] {
  width: 200pt;
  padding: 10px;
  border-radius: 5px;
  border: none;
  box-shadow: 1px 1px 5px rgba(0, 0, 0, 0.2);
  display: block;
  margin: 0 auto;
}

input[type="number"] {
  width: 120pt;
  padding: 10px;
  border-radius: 5px;
  border: none;
  box-shadow: 1px 1px 5px rgba(0, 0, 0, 0.2);
  display: block;
  margin: 0 auto;
}

.submitButton {
  background-color: #4caf50;
  font-size: large;
  font-weight: 900;
  color: white;
  border: none;
  border-radius: 25px;
  padding: 12px;
  cursor: pointer;
  box-shadow: 1px 1px 5px rgba(0, 0, 0, 0.2);
}

.submit-btn:hover {
  background-color: #3e8e41;
}
.errorMessage {
  font-size: 10pt;
  color: rgb(248, 5, 163);
}
.disabledButton {
  background-color: rgb(248, 5, 74);
  font-size: large;
  font-weight: 900;
  color: white;
  border: none;
  border-radius: 25px;
  padding: 12px;
  cursor: pointer;
  width: 300px;
  box-shadow: 1px 1px 5px rgba(0, 0, 0, 0.2);
  margin: 0 auto 20px;
}
.enabledButton {
  background-color: #4caf50;
  font-size: large;
  font-weight: 900;
  color: white;
  border: none;
  border-radius: 25px;
  padding: 12px;
  cursor: pointer;
  width: 300px;
  box-shadow: 1px 1px 5px rgba(0, 0, 0, 0.2);
  margin: 0 auto;
  margin: 0 auto 20px;
}
